.deliveries {
  margin: 50px;
}

#shipments {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

#shipments td,
#shipments th {
  border: 1px solid #ddd;
  padding: 8px;
}

#shipments tr:nth-child(even) {
  background-color: #f2f2f2;
}

#shipments tr:hover {
  background-color: #ddd;
}

#shipments th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #cfeaf1;
}

.round-button {
  display: block;
  width: 20px;
  height: 20px;
  line-height: 20px;
  border: 2px solid #f5f5f5;
  border-radius: 50%;
  text-align: center;
  text-decoration: none;
  background: #c43a31;
  box-shadow: 0 0 3px gray;
  font-size: 20px;
  font-weight: bold;
}
.round-button:hover {
  background: #777555;
}

.map-text {
  color: #2a8494;
  font-weight: bold;
  font-size: 14px;
}

.item-a {
  grid-area: map;
}
.item-b {
  grid-area: graph;
}
.item-c {
  grid-area: data;
}

.container {
  display: grid;
  grid-template-columns: 30% 30% 40%;
  grid-template-rows: 50vh;
  grid-template-areas:
    "map map data"
    "map map graph";
}
